import './Auth.scss';
import { useRef, useState } from 'react';
import request from '../../services/http';
import { Link } from 'react-router-dom';

const Register = () =>{

    const [hasError, setHasError] = useState(false)

    const emailInputRef = useRef(null)
    const passInputRef = useRef(null)

    console.log(emailInputRef, passInputRef);

    const handleSubmit = (e) => {
        e.preventDefault()

        request.post('/login', {
            data: {
                email: emailInputRef.current.value,
                password: passInputRef.current.value,
            }
        })
            .then(() => {
                alert('Succses')
            })
            .catch(() => {
                setHasError(true)
            })
    }

    return(
        <div className="container mt-5">
            <div className="row">

                <div className="col-md-3" />

                <div className="col-md-6">
                    <div className="card">

                        <div className="card-header">
                            <h1>Regsiter</h1>
                        </div>
                        <div className="card-body">

                            {
                                hasError ? (
                                    <div className="alert alert-danger" role="alert">
                                        Email or Password is invalid.
                                    </div>
                                ) : (
                                    <></>
                                )
                            }

                            <form onSubmit={handleSubmit}>
                                <div className="mb-3">
                                    <label htmlFor="exampleInputEmail1" className="form-label">Email address</label>
                                    <input ref={emailInputRef} type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" />
                                    <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
                                </div>
                                <div className="mb-3">
                                    <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
                                    <input ref={passInputRef} type="password" className="form-control" id="exampleInputPassword1" />
                                </div>
                                <div className="mb-3 form-check">
                                    <input type="checkbox" className="form-check-input" id="exampleCheck1" />
                                    <label className="form-check-label" htmlFor="exampleCheck1">Check me out</label>
                                </div>
                                <button type="submit" className="btn btn-primary">Submit</button>
                            </form>
                        </div>

                        <div className="card-footer">
                            <Link to="/">Ro'yhatdan Oldin O'tganmisiz?</Link>
                        </div>
                    </div>
                </div>

                <div className="col-md-3" />
            </div>
        </div>
    )
}

export default Register